<template>
  <div class="modal modal-application" :class="{ 'modal-application--active': $store.state.modals.applicationFront.isActive }">
    <div class="backdrop"></div>
    <div class="content">
      <div class="step step-1-1" v-show="activeClass === 'step-1-1'">
        <h2 class="step__question">Текст?</h2>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" @click="() => setActiveClass('step-1-2')">Да</button>
          <button class="step__btn step__btn--red">Нет</button>
        </div>
      </div>
      <div class="step step-1-2" v-show="activeClass === 'step-1-2'">
        <h2 class="step__question">Шрифт?</h2>
        <ul class="step__list">
          <li class="step__item">
            <input
              class="step__item-radiobtn visuallyhidden"
              id="font-1"
              type="radio"
              name="font-group"
              value="font-family: 'Pacifico', cursive"
              v-model="customFontFamily"
              checked
            >
            <label for="font-1" class="step__item-label" style="font-family: 'Pacifico', cursive">Рукописный</label>
          </li>
          <li class="step__item">
            <input
              class="step__item-radiobtn visuallyhidden"
              id="font-2"
              type="radio"
              name="font-group"
              value="font-family: 'Bebas Neue', sans-serif; font-weight: 700"
              v-model="customFontFamily"
            >
            <label for="font-2" class="step__item-label" style="font-family: 'Bebas Neue', sans-serif; font-weight: 700">Базовый прямой</label>
          </li>
          <li class="step__item">
            <input
              class="step__item-radiobtn visuallyhidden"
              id="font-3"
              type="radio"
              name="font-group"
              value="font-family: 'Bebas Neue', sans-serif; font-weight: 400"
              v-model="customFontFamily"
            >
            <label for="font-3" class="step__item-label" style="font-family: 'Bebas Neue', sans-serif; font-weight: 400">Тонкий</label>
          </li>
          <li class="step__item">
            <input
              class="step__item-radiobtn visuallyhidden"
              id="font-4"
              type="radio"
              name="font-group"
              value="font-family: 'Ruslan Display', cursive"
              v-model="customFontFamily"
            >
            <label for="font-4" class="step__item-label" style="font-family: 'Ruslan Display', cursive">Готический</label>
          </li>
          <li class="step__item">
            <input
              class="step__item-radiobtn visuallyhidden"
              id="font-5"
              type="radio"
              name="font-group"
              value="font-family: 'Pattaya', sans-serif"
              v-model="customFontFamily"
            >
            <label for="font-5" class="step__item-label" style="font-family: 'Pattaya', sans-serif">Как деньги светы</label>
          </li>
        </ul>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" @click="() => setActiveClass('step-1-3')">Далее</button>
          <button class="step__btn step__btn--red" @click="() => setActiveClass('step-1-1')">Назад</button>
        </div>
      </div>
      <div class="step step-1-3" v-show="activeClass === 'step-1-3'">
        <div class="step__preview">
          <p class="step__preview-text" :style="customFontFamily">{{ text }}</p>
        </div>
        <div class="step__text-wrapper">
          <label class="step__text-input-label" for="text-input">Пиши текст ниже</label>
          <input class="step__text-input" id="text-input" type="text" :style="customFontFamily" v-model="text">
        </div>
        <div class="step__btns-wrapper">
          <button class="step__btn step__btn--green" @click="() => setActiveClass('step-1-4')">Далее</button>
          <button class="step__btn step__btn--red" @click="() => setActiveClass('step-1-2')">Назад</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ApplicationFront',
  data() {
    return {
      activeClass: 'step-1-1',
      customFontFamily: 'font-family: \'Pacifico\', cursive',
      text: ''
    }
  },
  methods: {
    setActiveClass(newClass) {
      this.activeClass = newClass
    }
  }
}
</script>

<style scoped lang="scss">
  @import url('https://fonts.googleapis.com/css2?family=Pacifico&family=Pattaya&family=Ruslan+Display&display=swap');
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    display: none;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  .modal-application--active {
    display: flex;
  }

  .backdrop {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }

  .content {
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-width: 400px;
    padding: 50px;
    text-align: center;
    background-color: #fff;
  }

  .step {
    width: 100%;
  }

  .step__question {
    margin-bottom: 20px;
    font-family: 'Bebas Neue', sans-serif;
    font-size: 40px;
  }

  .step__btns-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .step__btn {
    width: 50%;
    padding: 20px 40px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    transition: background-color 0.2s ease-in;
  }

  .step__btn--green {
    color: #fff;
    background-color: #19a204;

    &:hover {
      background-color: #149203;
    }
  }

  .step__btn--red {
    margin-left: 10px;
    color: #fff;
    background-color: #be2a28;

    &:hover {
      background-color: #a42220;
    }
  }

  .step__btn--wide {
    width: 100%;
  }

  .step__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 20px;
    list-style: none;
  }

  .step__item-label {
    position: relative;
    display: inline-block;
    margin-bottom: 5px;
    padding-left: 50px;
    font-size: 24px;
    font-weight: 400;
    line-height: 30px;
    cursor: pointer;

    &:hover {
      color: orangered;
    }
  }

  .step__item-label::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    width: 30px;
    height: 30px;
    border: 3px solid #000;
  }

  .step__item-label::after {
    content: '';
    position: absolute;
    top: 6px;
    left: 6px;
    display: block;
    width: 18px;
    height: 18px;
    background-color: #000;
    transform: scale(0);
    opacity: 0;
    transition: transform 0.2s ease-in, opacity 0.2s ease-in;
  }

  .step__item-label:hover::before {
    border-color: orangered;
  }

  .step__item-label:hover::after {
    background-color: orangered;
  }

  .step__item-radiobtn:checked + .step__item-label::after {
    transform: scale(1);
    opacity: 1;
  }

  .step__text-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }

  .step__text-input-label {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 30px;
  }

  .step__text-input {
    padding: 10px 20px;
    font-size: 30px;
    line-height: 30px;
    border: none;
    border-bottom: 1px solid #aaa;
    text-align: center;
  }

  .step__preview {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100px;
    margin-bottom: 20px;
    padding: 20px;
    background-color: #ccc;
  }

  .step__preview::after {
    content: 'превью';
    position: absolute;
    bottom: 0;
    right: 0;
    font-size: 16px;
    line-height: 1;
    color: #aaa;
  }

  .step__preview-text {
    font-size: 30px;
    line-height: 1.25;
  }
</style>
